@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {

  margin: 0;

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",

    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",

    sans-serif;

  -webkit-font-smoothing: antialiased;

  -moz-osx-font-smoothing: grayscale;
  height: 100%;

}

 

code {

  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",

    monospace;

}

body {
  font-size: 1.2em;
  font-family: 'Roboto', sans-serif; }

/** Responisve css **/
@media only screen and (max-width: 500px) {
  body {
    font-size: 1em; }
  main {
    height: calc(100vh - 300px) !important; }
  .Footer {
    position: fixed !important;
    bottom: 0px !important; }
  .searchContainer,
  .carousel,
  .keyFeatures,
  .FooterCnt ul {
    display: none; }
  .header {
    width: 100%; }
    .header .logo {
      width: auto; }
  .bgWhite {
    background: #fff;
    padding: 10px;
    border-radius: 0px !important;
    border: 1px solid #dcdcdc; }
    .bgWhite #forgotPWD {
      padding-top: 20px;
      margin-bottom: 0px;
      font-size: 14px; }
    .bgWhite #signUp {
      display: none; }
  .form-control {
    border: none !important;
    border-bottom: 1px solid #ced4da !important;
    border-radius: 0px !important; }
  .btn-block {
    width: auto !important;
    float: right;
    padding: 8px 40px !important; }
  p {
    clear: both;
    margin-top: 5px; }
  .container-fluid {
    position: relative;
    top: calc(50vh - 200px);
    z-index: 2; }
  .globalLinks-mob {
    display: block !important; }
    .globalLinks-mob ul,
    .globalLinks-mob li {
      list-style-type: none; }
    .globalLinks-mob li {
      float: left;
      padding: 0px 20px;
      border-right: 1px solid #ced4da; }
      .globalLinks-mob li a {
        color: #666666;
        font-size: 14px; }
    .globalLinks-mob li:last-child {
      border-right: none; } }

.globalLinks-mob {
  display: none; }

.header {
  padding: 10px 15px;
  display: flex; }
  .header div {
    width: 50%; }
  .header .logo img {
    display: inline-flex; }
  .header .logo h2 {
    color: #fff;
    font-size: 32px;
    font-weight: normal;
    display: inline-block;
    vertical-align: middle;
    margin-top: -10px; }
  .header .searchContainer {
    text-align: right;
    padding-right: 10px; }
    .header .searchContainer img {
      vertical-align: middle;
      margin-left: 15px; }
  .header .search {
    width: 48%;
    border-radius: 10px;
    padding: 6px;
    border: 1px solid #fff;
    height: 40px; }

main {
  background: #13b7e4;
  background: linear-gradient(136deg, #13b7e4 42%, #597aef 35%);
  height: 100%; }

.keyFeatures ul {
  list-style-type: none; }

.keyFeatures ul li {
  color: #fff;
  font-size: 25px;
  line-height: 35px;
  font-family: roboto;
  padding-bottom: 15px; }

.form-group {
  width: auto; }
  .form-group .form-check-input {
    margin-left: 0px !important;
    float: left;
    left: 20px; }
  .form-group label {
    color: #666666;
    margin-left: 20px;
    float: left;
    position: relative;
    left: 5px;
    top: -1px; }

.bgWhite {
  background: #fff;
  border-radius: 10px;
  padding: 10px; }

.Footer {
  position: relative;
  bottom: -100px;
  min-height: 60px;
  width: 100%;
  padding: 20px 5px; }

.FooterCnt {
  text-align: center; }
  .FooterCnt ul {
    list-style-type: none; }
    .FooterCnt ul li {
      display: inline-block;
      padding-left: 5px; }
      .FooterCnt ul li a {
        text-decoration: none;
        color: #000;
        font-size: 15px;
        line-height: 21px;
        border-bottom: 1px solid; }
      .FooterCnt ul li a:hover {
        text-decoration: none; }

.textDn {
  margin-bottom: 0;
  padding-top: 5px; }

.no-border {
  border-top: none !important;
  border-left: none !important;
  border-right: none !important;
  border-radius: 0 !important;
  box-shadow: none !important;
  /* You may want to include this as bootstrap applies these 
        styles too */ }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #61dafb; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.carousel-item > img {
  width: 100%; }

.carousel-control-prev,
.carousel-control-next {
  cursor: pointer; }

.carousel-item,
.carousel-item.active {
  align-items: center; }

.nav-tabs .nav-item.show .nav-link,
.nav-tabs .nav-link.active {
  font-weight: bold !important; }

.nav-tabs .nav-item {
  margin-bottom: 0px !important; }

.nav-tabs .nav-link {
  color: #495057 !important;
  background-color: #fff !important;
  border-color: #dee2e6 #dee2e6 #fff !important; }


